import React, { Suspense } from "react";
import {
    BrowserRouter as Router, Route, Switch, Redirect 
} from "react-router-dom";
import { PageLoading } from "../common/components/PageLoading";
import { createAdminRoutes } from "../modules/admin/createAdminRoutes";
import { createAppRoutes } from "../modules/app/createAppRoutes";

const NotFoundLazy = React.lazy(() => import(
    /* webpackChunkName: "NotFound" */
    "../components/notFound/NotFound"
));

export const ApplicationRouter = () => (
    <Suspense fallback={<PageLoading />}>
        <Router>
            <Switch>
                <Redirect exact path="/" to="/app" />
                {createAdminRoutes()}
                {createAppRoutes()}
                <Route path="*" component={NotFoundLazy} />
            </Switch>
        </Router>
    </Suspense>
);
