import { action, observable } from 'mobx';
import { StoreBase } from "../../common/utils/StoreBase";

const uuid = require('uuid');


export class AccountInfoStore extends StoreBase {
    @observable
    isLoadingUserInfo = true;

    @action
    setIsLoadingUserInfo(isLoadingUserInfo: boolean) {
        this.isLoadingUserInfo = isLoadingUserInfo;
    }

    @observable
    currentUserId = '';

    @action
    setCurrentUserId(currentUserId) {
        this.currentUserId = currentUserId;
    }

    @observable
    email = '';

    @action
    setEmail(email: string) {
        this.email = email;
    }

    @observable
    currentUserName = '';

    @action
    setCurrentUserName(currentUserName: string) {
        this.currentUserName = currentUserName;
    }


    @observable
    token = '';

    @action
    setToken(token: string) {
        this.token = token;
    }
}
