import React from "react";
import { Route } from "react-router";
import AppWrapper from "./AppWrapper";
import { IndexPageLazy, LazyRoutePageList, ModuleRootRoutePath } from "./lazyPages";


export function createAppRoutes() {
    return (
        <Route path={ModuleRootRoutePath} component={AppWrapper}>
            <Route path={ModuleRootRoutePath} exact component={IndexPageLazy}/>
            {LazyRoutePageList.map(({routePath, Component}) => {
                return (
                    <Route
                        key={routePath}
                        path={routePath}
                        component={Component}
                    />
                );
            })}
        </Route>
    );
}
