/* eslint-disable */
import React from 'react';

export const ModuleRootRoutePath = "/admin";
export const LazyRoutePageList = [];

export const IndexPageRoutePath = "/admin/index";
export const IndexPageLazy = React.lazy(() => import(
    /* webpackChunkName: "admin/IndexPage.tsx" */
    "./IndexPage"
    ));
LazyRoutePageList.push({
    routeName: 'IndexPageRoutePath',
    routePath: IndexPageRoutePath,
    Component: IndexPageLazy,
});

export const LoginPageRoutePath = "/admin/login";
export const LoginPageLazy = React.lazy(() => import(
    /* webpackChunkName: "admin/LoginPage.tsx" */
    "./LoginPage"
    ));
LazyRoutePageList.push({
    routeName: 'LoginPageRoutePath',
    routePath: LoginPageRoutePath,
    Component: LoginPageLazy,
});
