import React from "react";
import { Route } from "react-router";
import { IndexPageLazy, LazyRoutePageList, ModuleRootRoutePath } from "./lazyPages";


const AdminWrapperLazy = React.lazy(() => import(
    /* webpackChunkName: "AppWrapper" */
    "./AdminWrapper"
    ));


export function createAdminRoutes() {
    return (
        <Route path={ModuleRootRoutePath} component={AdminWrapperLazy}>
            <Route path={ModuleRootRoutePath} component={IndexPageLazy}/>

            {LazyRoutePageList.map(({ routePath, Component }) => {
                return (
                    <Route
                        key={routePath}
                        path={routePath}
                        component={Component}
                    />
                );
            })}
        </Route>
    );
}
